.uneCarte {
  display: flex;
  flex-direction: column;
  width: 25%;
  margin: 10px;
  overflow: hidden;
  position: relative;
}
.uneCarte:hover {
  transition: transform 400ms;
  transform: scale(1.1);
}
.uneCarte img {
  -o-object-fit: cover;
     object-fit: cover;
  height: 340px;
  width: 100%;
  border-radius: 20px;
}
.uneCarte div {
  display: flex;
  flex-direction: column;
  height: 340px;
  width: 100%;
  border-radius: 20px;
  justify-content: flex-end;
  position: absolute;
  bottom: 0;
  background: linear-gradient(rgba(255, 255, 255, 0.2) 0%, rgba(0, 0, 0, 0.2) 50%);
}
.uneCarte h2 {
  font-size: 18px;
  font-weight: 500;
  color: #ffffff;
  padding: 0 10px;
}

/************** Responsive pour smartphone *****************/
@media all and (max-width: 768px) {
  .uneCarte {
    height: 255px;
    width: 100%;
    margin: 0 0 20px 0;
  }
  .uneCarte:hover {
    transform: scale(1.05);
  }
  .uneCarte img, .uneCarte div {
    height: 255px;
    width: 100%;
  }
  .uneCarte h2 {
    width: 40%;
  }
}/*# sourceMappingURL=carte.css.map */